import { GlobalComponent } from "../../components/global/global";
import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { ZipFileLoopPwdViewModel } from "../../Model/ViewModel/ZipFileLoopPwdViewModel";

/**
 * Generated class for the ZipFileLoopPwdModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-zip-file-loop-pwd-add-edit-modal",
  templateUrl: "zip-file-loop-pwd-add-edit-modal.html"
})
export class ZipFileLoopPwdAddEditModalPage {
  item: ZipFileLoopPwdViewModel;
  mode: string;
  CanEditBatch: boolean;
  title: string;
  ZipFileLoopPwd_type: string;
  change_mode:string;
  file_type_key:string;
  confirm_password:string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
   public loadingCtrl: LoadingController,
    private global: GlobalComponent,
    public modalCtrl: ModalController
  ) {

    this.mode = navParams.data.mode;
    this.change_mode=navParams.data.change_mode;
    this.file_type_key=navParams.data.file_type_key;
    this.item = new ZipFileLoopPwdViewModel();
    this.item.file_loop_seq=navParams.data.item.file_loop_seq;
    this.item.zip_seq=navParams.data.item.zip_seq;
    this.item.job01=navParams.data.item.job01;
    this.item.job02=navParams.data.item.job02;
    this.item.job03=navParams.data.item.job03;
    this.item.lc01=navParams.data.item.lc01;
    this.item.lc02=navParams.data.item.lc02;
    this.item.lc03=navParams.data.item.lc03;

    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;

    this.CanEditBatch = navParams.data.CanEditBatch;

    this.title=navParams.data.title;
  }


  Save() {
    if(this.confirm_password!=this.item.pwd)
    {
      this.global.showPopup("密碼錯誤","確認密碼不一致");
      return;
    }
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ZipFileLoopPwdModalPage");
  }
}



